@media screen and (min-width: 1024px) {
    body {
        width: 80%;
        padding: 50px 10%;
    }

    h4 {
        font-size: 15px;
    }

    h4 + img {
        height: 15px;
    }

    .route-container {
        width: 80%;
        margin-left: 10%;
        padding: 30px;
        left: 0px;
    }

    .route-container p {
        width: 100%;
        margin-left: 0%;
    }

    select {
        font-size: 16px;
        border-radius: 26px;
    }

    .directions-container label {
        width: fit-content;
    }


    .directions-container label + input {
        margin-left: 30%;
    }
}